import { Component, OnInit, Directive, OnChanges } from '@angular/core';
import { Input } from '@angular/core';
import { LoggerService } from '../../service/logger.service';

let nextId = 1;

@Directive()
export class PeekABooDirective implements OnInit {
  constructor(private logger: LoggerService) { }

  // OnInit 인터페이스에서 정의하는 `ngOnInit` 메소드를 구현합니다.
  ngOnInit() { this.logIt(`OnInit`); }

  logIt(msg: string) {
    this.logger.log(`#${nextId++} ${msg}`);
  }
}

@Component({
  selector: 'app-peek-a-boo',
  template: '<p>Now you see my hero, {{name}}</p>',
  styles: ['p {background: LightYellow; padding: 8px}']
})
export class PeekABooComponent implements OnChanges, OnInit {
  @Input()  name: string;

  constructor(private logger : LoggerService) { 
    //super(logger);

    const is = this.name ? 'is' : 'is not';
    this.logger.log(`name ${is} known at construction`);
  }

  ngOnInit(): void {
    this.logger.log('onInit====');
  }

  ngOnChanges() {
    this.logger.log('OnChanges====');
  }

}
