import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-keyup',
  template: `
  <input (keyup)="onKey($event)">
  <p>{{values}}</p>
`
})
export class KeyupComponent implements OnInit {
  values = '';
  
  constructor() { }

  ngOnInit() {
  }

  onKey(event:any) {
    //this.values += event.target.value + ' | ';
    this.values += event.key + ' | ';
  }

  onClear() {
    this.values = '';
  }


}